<template>
    <v-app id="login" class="primary">
        <v-content>
            <v-container fluid fill-height>
                <v-layout align-center justify-center>
                    <v-flex xs12 sm8 md4 lg4>
                        <v-card class="elevation-1 pa-3">
                            <v-card-text>
                                <div class="layout column align-center">
                                    <h1 class="flex my-4 primary--text">查重系统登录</h1>
                                    <h4 class="flex my-4 primary--text">by 凡人放弃吧</h4>
                                </div>                
                                <v-form>
                                    <v-text-field append-icon="person" name="login" label="账户" type="text" v-model="model.acount"></v-text-field>
                                    <v-text-field append-icon="lock" name="password" label="密码" id="password" type="password" v-model="model.password"></v-text-field>
                                    <v-switch
                                        v-model="model.type"
                                        :label="name"
                                        >
                                    </v-switch>
                                </v-form>
                            </v-card-text>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn block color="primary" @click="login" :loading="loading">登录</v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-flex>
                </v-layout>
            </v-container>
        </v-content>
        <v-snackbar
      v-model="successBar"
      :timeout="2000"
      top
    >
      登录成功
      <v-btn
        color="pink"
        flat
        @click="successBar = false"
      >
        关闭
      </v-btn>
    </v-snackbar>
    <v-snackbar
      v-model="errorBar"
      :timeout="2000"
      top
    >
      账户名或密码错误
      <v-btn
        color="pink"
        flat
        @click="errorBar = false"
      >
        关闭
      </v-btn>
    </v-snackbar>
    <v-snackbar
      v-model="invalidBar"
      :timeout="2000"
      top
    >
      用户名或密码不能为空
      <v-btn
        color="pink"
        flat
        @click="invalidBar = false"
      >
        关闭
      </v-btn>
    </v-snackbar>
    <v-snackbar
      v-model="neterrBar"
      :timeout="2000"
      top
    >
      用户名或密码不能为空
      <v-btn
        color="pink"
        flat
        @click="neterrBar = false"
      >
        关闭
      </v-btn>
    </v-snackbar>
    </v-app>
</template>

<script>
// import tokenService from "../apis/tokenService.js";
export default {
  data: () => ({
    successBar: false,
    errorBar: false,
    invalidBar: false,
    neterrBar: false,
    loading: false,
    model: {
      acount: "",
      password: "",
      type: false
    }
  }),
  computed: {
    name() {
      return this.model.type ? "教师" : "学生";
    }
  },
  methods: {
    login() {
      if (this.model.acount === "" || this.model.password === "") {
        this.invalidBar = true;
        return;
      }
      this.loading = true;
      let _this = this;
      let url = this.model.type ? "/apis/teacher/token" : "/apis/student/token";
      this.$getRequest(url, this.model).then(res => {
        _this.loading = false;
        if (res && res.data) {
          _this.successBar = true;
          setTimeout(() => {
            this.$router.push(_this.model.type ? "/teacherHome" : "/home");
          }, 1000);
        } else {
          _this.errorBar = true;
        }
      }).catch(err => {
        alert(err);
      });
    }
  }
};
</script>

<style scoped lang="css">
#login {
  height: 50%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  z-index: 0;
}
</style>